<template>
  <div class="personnal-detail-wrap">
    <div class="personnal-detail-wrap-title">我的收藏</div>
    <el-tabs class="personnal-detail-wrap-tabs" v-model="activeName" @tab-click="tabClick">
      <el-tab-pane label="作业" name="first"></el-tab-pane>
      <el-tab-pane label="笔记" name="second"></el-tab-pane>
      <el-tab-pane label="题目" name="third"></el-tab-pane>
      <el-tab-pane label="活动" name="forth"></el-tab-pane>
      <el-tab-pane label="关注的老师" name="fifth"></el-tab-pane>
    </el-tabs>
    <div class="personnal-detail-wrap-container">
      <common-card :option="cardOption" v-for="(c,i) in cards" :key="i" :datas="c"/>
    </div>
    <bottom-pager :total="total" @change="pageChange"/>
  </div>
</template>
<script>
import commonCard from "../../../components/personal/student/commonCard.vue";
import bottomPager from "../../../components/personal/bottomPager.vue";
export default {
  components: {
    commonCard,
    bottomPager
  },
  data() {
    return {
      total: 1,
      activeName: "first",
      cardOption: {
        icon: "icon-homework",
        btnText: "查看详情"
      },
      cards: [
        {
          title: "大学英语最易错的语法+单词+书写之时大汇总",
          descs: [
            {
              text: "2019-04-10  15:32:46  发布"
            },
            {
              text: "江丽颜老师"
            },
            {
              text: "英语作业"
            }
          ]
        }
      ]
    };
  },
  methods: {
    tabClick() {},
    pageChange(currentPage) {}
  }
};
</script>
<style lang="less" scoped>
</style>



